import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import * as config from '../../config';
import Meeting from './Meeting';

class Host extends Component {

  state = {
    role: 'host',
    username: '',
    title: '',
    errorMsg: '',
    showError: false,
    isGoToMeeting: false,
  }

  constructor() {
    super ();
    this.inputRef = React.createRef();
    this.baseHref = config.BASE_HREF;
  }

  componentDidMount() {
    const qs = new URLSearchParams(this.props.location.search);
    const room = qs.get('room');
    const username = qs.get('username');

    this.setState({
      title: room,
      username
    });

    if(username && room) {
      const data = {
        username,
        title: room,
        role: this.state.role,
        playbackURL: config.DEFAULT_VIDEO_STREAM,
      }

      sessionStorage.setItem(`chime[${room}]`, JSON.stringify(data));

      this.setState({isGoToMeeting: true});
      // this.props.history.push(`${this.baseHref}/meeting?room=${room}&username=${username}`);
    }
  }

  handleNameChange = e => {
    this.setState({ username: e.target.value })
  }

  handleJoinRoom = (e) => {
    e.preventDefault();
    e.stopPropagation();

    this.joinRoom();
  }

  joinRoom = async () => {
    const { username, title } = this.state;
    const data = {
      username,
      title,
      role: this.state.role,
      playbackURL: config.DEFAULT_VIDEO_STREAM,
    }
    sessionStorage.setItem(`chime[${title}]`, JSON.stringify(data));
    this.setState({isGoToMeeting: true});
    this.props.history.push(`${this.baseHref}/NA25KW8TFW?room=${title}&username=${username}`);
  }

  render() {
    const { username, isGoToMeeting } = this.state;
    const joinRoomDisabled = !username;

    if (isGoToMeeting) {
      return <Meeting chime={this.props.chime}/>
    }
    return (
      <div className="welcome form-grid" onClick={this.handleClick}>

        <div className="welcome__intro">
          <div className="intro__inner formatted-text">
            <h1>Join as a Host</h1>
          </div>
        </div>

        <div className="welcome__content pd-4">
          <div className="content__inner">
            <h2 className="mg-b-2">Hey there!</h2>
            <form action="">
              <fieldset className="mg-b-2">
                <input type="text" placeholder="Your name" value={username} ref={this.inputRef} onChange={this.handleNameChange} />
                <button className="mg-t-1 btn btn--primary" disabled={joinRoomDisabled} onClick={this.handleJoinRoom}>Join room</button>
              </fieldset>
            </form>
          </div>
        </div>

      </div>
    )
  }
}

export default withRouter(Host);
